<template>
  <div class="ui top attached menu">
    <router-link
      to="/"
      class="item name"
    >
      SimpleCoinCap
    </router-link>
    <router-link
      to="/"
      class="ui icon item"
      :class="{'item__active' : $route.path === '/'}">
      <i class="home icon"></i>
    </router-link>
    <router-link
      to="/about"
      class="ui icon item"
      :class="{ 'item__active' : $route.path === '/about'}">
      <i class="question icon"></i>
    </router-link>
    <div class="right menu">
      <div class="ui right aligned category search item">
        <div class="ui transparent icon input">
          <v-select
            id="v-select"
            placeholder="Search..."
            :options="coins"
            @input="selectCoin"
            label="name"
          >
            <template slot="option" slot-scope="option">
              <img :src="`https://chasing-coins.com/api/v1/std/logo/${option.symbol}`" class="select__logo" />
              <span class="select__name">{{option.name}}</span>
            </template>
          </v-select>
          <i class="search link icon"></i>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
  import {mapGetters} from 'vuex';

  export default {
    name: 'AppHeader',
    computed: {
      ...mapGetters([
        'coins'
      ])
    },
    methods: {
      selectCoin(val) {
        if (val){
          this.$router.push(`/currency/${val.website_slug}`);
        }
      }
    }
  }
  name: 'AppHeader'

</script>


<style src="@/assets/styles/app-header.css">

</style>


